      'NV50 Series Needle Valves',
      'Instrument Tubes',
      'Condensate Pots',
      'Air Distribution Manifolds',
      'New Release',
    ], 
    
	}),
  methods: {
    open(){
      let routeData = this.$router.resolve({name: 'routeName', query: {data: "someData"}});
      window.open(routeData.href, '_blank');
    },
    onLabel(id){
      if(this.$route.params.subtitle === id)
        return;

      const div = document.getElementById(id)
      div.style.background= "rgb(41, 94,152)";
    },
    outLabel(id){

      if(this.$route.params.subtitle === id)
        return;

      const div = document.getElementById(id)
      div.style.background= "rgb(82, 82,82)";
    },

    clickLabel(id){

      if(this.$route.params.subtitle === id)
        return;

      document.getElementById(this.$route.params.subtitle).style.background= "rgb(82, 82,82)";
      document.getElementById(id).style.background= "rgb(41, 94, 152)";


      this.subtitle = id;
      this.$route.params.subtitle = id;
      this.$store.state.subtitle = id;


    },

  },
  computed : {
    getSubtitle(){return this.$store.getters.getSubtitle },
  },
  watch : {
    getSubtitle(v){
      this.subtitle = v;
      const div = document.getElementById(v)
      div.style.background= "rgb(41, 94,152)";

    }
  },

}
</script>
