import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    isMobile: true,
    subtitle: '',
  },

  getters: {
    getSubtitle(state){return state.subtitle}
  },
  mutations: {
    setWhetherMobileBrowser(state, isMobileBrowser){
      state.isMobile = isMobileBrowser;
    }, 
  },
  actions: {
  },
  modules: {
  }
})
